body {
  margin: 0;
  padding: 0;
}

.navbar
{
    position: fixed;
    top:0;
    width: 100%;
    background-image: -webkit-linear-gradient(left, rgba(15, 3, 44, 0.9),rgba(60, 8, 112, 0.5));
    background-image: linear-gradient(to right, rgba(15, 3, 44, 0.9),rgba(60, 8, 112, 0.5));
    box-shadow: 2px 0 5px rgba(11, 0, 24, 0.733);
}

.navlist
{
    margin: 0;
    padding:0;
    list-style: none;
    display:-webkit-flex;
    display:flex;
}

.navlistelement
{
    padding: 2.5vh 3vw 2.5vh 3vw;
    margin-left: 1.5vw;
}

.corner
{
    margin-left: auto;
}

a
{
    text-decoration: none;
    font-family: 'Oswald', sans-serif;
    font-size: 1em;
    color:lightgoldenrodyellow;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
}

a:visited
{
    color:lightgoldenrodyellow;
}

a:hover
{
    color:  rgb(253, 182, 100);
}

.logocontainer
{
    grid-column: 1/2;
    grid-row: 2/3;
    display: -webkit-flex;
    display: flex;
    height: 64px;
    width: 64px;
    padding: 10px;
    margin-left: 3vw;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    /* background-image:radial-gradient(mediumaquamarine, mediumturquoise, rgb(19, 91, 94)); */
    background-color: mediumturquoise;
    border-radius: 50%;
    border: 3px solid rgb(11, 3, 44);
    box-shadow: 2px 2px 20px rgba(12, 1, 29, 0.692);
    text-shadow: 3px 3px 10px rgba(255, 68, 0, 0.699);
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -webkit-transition: background-color 1s linear, border-color 1s linear, box-shadow 1s linear, text-shadow 1s linear;
    transition: background-color 1s linear, border-color 1s linear, box-shadow 1s linear, text-shadow 1s linear;
}
.logocontainer:hover
{
    cursor: none;
    /* background-image:radial-gradient(gold, rgb(255, 125, 50), rgba(56, 0, 9, 0.856)); */
    background-color: rgb(238, 132, 11);
    box-shadow: 2px 2px 20px rgba(255, 68, 0, 0.829);
    border: 3px solid rgb(172, 83, 0);
    text-shadow: 4px 4px 20px rgb(255, 226, 98);
    -webkit-transition: all 1s linear;
    transition: all 1s linear;
}

.icon
{
    text-align: center;
    font-size: 3em;
}

.imagelinkcontainer
{
    grid-column: 2/3;
    grid-row: 3/span 2;
}

@-webkit-keyframes imagelinkformappear
{
    from {opacity: 0;}
    to {opacity: 1;}
}

@keyframes imagelinkformappear
{
    from {opacity: 0;}
    to {opacity: 1;}
}
.imagelinkform
{
    -webkit-animation: imagelinkformappear;
            animation: imagelinkformappear;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
}

@-webkit-keyframes captionanimation{
    0% {color:goldenrod; opacity: 0}
    25% {color:orange}
    50% {color: gold}
    100% { color:lightgoldenrodyellow; opacity: 1;}
}

@keyframes captionanimation{
    0% {color:goldenrod; opacity: 0}
    25% {color:orange}
    50% {color: gold}
    100% { color:lightgoldenrodyellow; opacity: 1;}
}
.formlinkcaption
{
    color:lightgoldenrodyellow;
    font-size: 1em;
    font-family: 'Roboto Condensed', sans-serif;
    -webkit-animation: captionanimation;
            animation: captionanimation;
    -webkit-animation-duration: 3s;
            animation-duration: 3s;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.forminputs
{
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.formlinkurl
{
    border: 2px solid lightgoldenrodyellow;
    background-color: rgb(14, 3, 39);
    border-radius: 5px 2px 10px 2px;
    width: 80%;
    font-size: 1em;
    font-family: 'Titillium Web', sans-serif;
    text-align: center;
    color:lightsalmon;
    outline: none;
    -webkit-transition: border-color 1s ease-in;
    transition: border-color 1s ease-in;
}
.formlinkurl:hover
{
    border-color: rgb(250, 169, 92);
    -webkit-transition: border-color 1s ease-in;
    transition: border-color 1s ease-in;
}

.formlinkdetect
{
    text-align: center;
    border-radius: 100%;
    border: 3px solid rgb(11, 3, 44);
    box-shadow: 2px 2px 20px rgba(12, 1, 29, 0.692);
    text-shadow: 1px 0px 2px rgba(16, 1, 39, 0.699);
    background-color: mediumturquoise;
    color:rgb(5, 5, 41);
    font-size: 1.5em;
    font-family: 'Anton', sans-serif;
    outline: none;
    height: 12vh;
    width: 12vh;
    padding: 10px;
    margin-top: 20px;
    -webkit-transition: background-color 1s ease, border-color 1s ease, box-shadow 1s ease;
    transition: background-color 1s ease, border-color 1s ease, box-shadow 1s ease;
}
.formlinkdetect:hover
{
    color: gold;
    background-color: rgb(238, 132, 11);
    box-shadow: 2px 2px 20px rgba(255, 68, 0, 0.829);
    border: 3px solid rgb(172, 83, 0);
    text-shadow: 4px 4px 20px rgb(255, 226, 98);
    cursor: none;
    -webkit-transition: all 1s linear;
    transition: all 1s linear;
}
