.titlecontainer
{
    grid-column: 2/3;
    grid-row: 2/3;
}

@-webkit-keyframes titleanimation
{
    0% {color: rgba(252, 240, 169, 0.788)}
    50% {color: rgba(250, 250, 210, 0.76)}
    75% {color: rgba(156, 236, 234, 0.726)}
    100% {color: rgba(252, 240, 169, 0.788)}
}

@keyframes titleanimation
{
    0% {color: rgba(252, 240, 169, 0.788)}
    50% {color: rgba(250, 250, 210, 0.76)}
    75% {color: rgba(156, 236, 234, 0.726)}
    100% {color: rgba(252, 240, 169, 0.788)}
}

.title
{
    font-size: 5vw;
    font-family: 'Fredericka the Great', cursive;
    text-align: center;
    letter-spacing: 1vw;
    color:rgba(252, 240, 169, 0.788);
    text-shadow: 2px 5px 25px rgb(14, 0, 53);
    -webkit-animation: titleanimation;
            animation: titleanimation;
    -webkit-animation-duration: 10s;
            animation-duration: 10s;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}
.srcimagecontainer
{
    grid-column: 2/3;
    grid-row: 2/span 2;
}

.imagecontrols
{
    position: absolute;
    margin-left: calc(100vmin - 60vmin - 60vmin/4);
}
.image
{
    width: 60vmin;
    height: auto;
    -webkit-align-self: center;
            align-self: center;
    justify-self: center;
    border-radius: 15px 2px 10px 5px;
    box-shadow: 2px 2px 10px rgb(17, 1, 31);
    border: 3px solid rgb(11, 3, 44);
}
.clipper
{
    opacity: 0;
    position: absolute;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
    border: 2px solid orange;
    border-radius: 2px;
    box-shadow: 2px 2px 10px rgb(77, 38, 1);
}
.imgopsbarcontainer
{
    grid-column: 2/3;
    grid-row: 4/span 1;
    display: -webkit-flex;
    display: flex;
