}

.imgopsbar{
    background-image: -webkit-linear-gradient(left, rgba(15, 3, 44, 0.8),rgba(60, 8, 112, 0.4));
    background-image: linear-gradient(to right, rgba(15, 3, 44, 0.8),rgba(60, 8, 112, 0.4));
    box-shadow: 2px 0 5px rgba(11, 0, 24, 0.7337);
    border-radius: 15px 5px 15px 5px;
    width: 50%;
    z-index: 0;
}

.navlist
{
    margin: 0;
    padding: 0;
    list-style: none;
    display: -webkit-flex;
    display: flex;
}

.navlistelement
{
    padding: 10px;
    margin-left: 1.5em;
    text-decoration: none;
    font-family: 'Oswald', sans-serif;
    font-size: 1em;
    color:lightgoldenrodyellow;
    -webkit-user-select: none;
       -moz-user-select: none;
